import { ROLE } from '@/interface/IRole';
import Cookies from 'js-cookie';
const actions = {
    async init({ dispatch, rootState }) {
        try {
            const checkToken = Cookies.get('access_token');
            if (!checkToken) {
                return Promise.resolve(false);
            }
            dispatch('user/updateToken', checkToken, { root: true });
            if (!rootState.user.fullname || rootState.user.fullname !== '') {
                await dispatch('user/getUserInfo', {}, { root: true });
            }
            if (rootState.user.role_id === ROLE.EXPERT || rootState.user.role_id === ROLE.UNIVERSITY_USER || rootState.user.role_id === ROLE.UNIVERSITY_ADMIN) {
                await dispatch('university/insertUniversity', rootState.user.university_id, { root: true });
            }
            return Promise.resolve(true);
        }
        catch (error) {
            return Promise.resolve(false);
        }
    },
    async resetData({ dispatch }) {
        // await dispatch('user/getUserInfo', {}, {root: true})
        // dispatch('user/updateToken', '', { root: true })
        sessionStorage.clear();
    },
};
export default actions;
