import Cookies from 'js-cookie';
import axios from 'axios';
import { API_URL } from '@/config';
import { Message } from 'element-ui';
import { includes } from 'lodash';
const request = axios.create({
    baseURL: API_URL,
    withCredentials: false,
});
request.defaults.headers.put['Content-Type'] = 'application/x-www-form-urlencoded';
request.defaults.headers.common['Authorization'] = Cookies.get('access_token') !== null ? 'Bearer ' + Cookies.get('access_token') : '';
request.interceptors.response.use(response => {
    return response;
}, error => {
    if (!error.response || error.response.status >= 500) {
        error.message = 'Khng th kt ni n my ch';
        Message.error(error.message);
        return Promise.reject(error);
    }
    if (error.response.status < 500) {
        if (error.response.status === 401) {
            Message.error('Li ng nhp !');
            setTimeout(() => {
                if (!includes(location.href, 'dang-nhap')) {
                    location.href = '/dang-nhap';
                }
            }, 200);
            return Promise.reject(error);
        }
        else if (error.response.status === 400) {
            if (!error.response.data.message) {
                // eslint-disable-next-line @typescript-eslint/consistent-type-assertions
                const listErrors = Object.keys(error.response.data).map((key) => {
                    return error.response.data[key];
                });
                listErrors.forEach(value => {
                    value.forEach((err) => {
                        setTimeout(() => {
                            Message.error(err);
                        }, 100);
                    });
                });
            }
            else {
                Message.error(error.response.data.message);
            }
        }
        return Promise.reject(error);
    }
    if (error.response.status === 500) {
        Message.error('Chc nng ny ang c bo tr, chng ti s thng bo khi bo tr hon thnh');
        // router.push({ name: 'login' });
        // sessionStorage.clear()
    }
    return Promise.reject(error);
});
export default request;
