const labelDayOfWeek = ['Ch nht', 'Th 2', 'Th 3', 'Th 4', 'Th 5', 'Th 6', 'Th 7'];
// const labelDay = ['Hm nay', 'Hm qua'];
export function dateFormatSort(time) {
    const date = new Date(time);
    return `${date.getFullYear()}/${date.getMonth() + 1}/${date.getDate()}`;
}
export function dateFormatNameSort(time) {
    const date = new Date(time);
    return `${date.getDate()} thng ${date.getMonth() + 1}`;
}
export function dateFormat(time) {
    const date = new Date(time);
    const minutes = date.getMinutes() < 10 ? `0${date.getMinutes()}` : date.getMinutes();
    return `${date.getHours()}h:${minutes} ${labelDayOfWeek[date.getDay()]}, ${date.getDate()}/${date.getMonth() + 1}/${date.getFullYear()}`;
}
export function dateFormatYear(time) {
    const date = new Date(time);
    return date.getFullYear();
}
export function percent(data, hasPercent = false) {
    const result = typeof data === 'string' ? parseInt(data) * 100 : data * 100;
    return hasPercent ? `${result.toFixed(2)} %` : result.toFixed(2);
}
export function toPrecision(data, decimal = 2) {
    if (typeof data !== 'number') {
        return data;
    }
    if (Math.floor(data) === data) {
        return data;
    }
    return data.toFixed(decimal);
}
