import request from '@/plugins/request';
const actions = {
    async insertUniversity({ commit }, uniId) {
        try {
            const data = await request.get(`university/${uniId}/${new Date().getFullYear()}`);
            commit('UNI_INSERT', data.data.data.university);
            return Promise.resolve();
        }
        catch (error) {
            return Promise.reject(error);
        }
    },
    setUniData({ commit }, data) {
        commit('UNI_INSERT', data);
    },
    clearUniversityData({ commit }) {
        commit('SET_UNI_DATA', {
            id: -1,
            name_vi: '',
            name_en: null,
            short_name_vi: null,
            short_name_en: null,
            name_before: null,
            governing_body: null,
            address: null,
            phone_number: null,
            fax_number: null,
            email: null,
            website: null,
            founded_year: null,
            k1_start_date: null,
            k1_end_date: null,
            institution_type: null,
            institution_type_other: null,
            training_type: null,
            training_type_other: null,
        });
    },
};
export default actions;
