const getters = {
    getUserRole: state => {
        return state.role_id;
    },
    getUserInfo: state => {
        return state;
    },
    isPermissions: state => (role) => {
        return role.indexOf(state.role_id) !== -1;
    },
    getUniversityID: state => {
        return state.university_id;
    }
};
export default getters;
