import request from '@/plugins/request';
import { Message } from 'element-ui';
import Cookies from 'js-cookie';
const actions = {
    async login({ commit }, data) {
        try {
            const result = await request.post('auth/login', data);
            if (result.data.success) {
                await Cookies.set('access_token', result.data.access_token);
                Cookies.set('user_info', JSON.stringify(result.data.user));
                request.defaults.headers.common['Authorization'] = 'Bearer ' + result.data.access_token;
                commit('USER_INFO', result.data.user);
                commit('UPDATE_TOKEN', result.data.access_token);
                Message.success('ng nhp thnh cng!');
                // await dispatch('getUserInfo')
                // await commit('USER_INFO', result.data.user);
                return Promise.resolve();
            }
            else {
                return Promise.reject(result.data.message);
            }
        }
        catch (e) {
            return Promise.reject(e);
        }
    },
    async getUserInfo({ commit }) {
        try {
            const user = await request.get('user/me');
            await commit('USER_INFO', user.data.data.user);
            Cookies.set('user_info', JSON.stringify(user.data.data.user));
            Message.success('Ly thng tin thnh cng!');
        }
        catch (e) {
            return Promise.reject(e);
        }
    },
    updateToken({ commit }, access_token) {
        commit('UPDATE_TOKEN', access_token);
    },
    async logout({ commit }) {
        try {
            request.post('auth/logout');
            Cookies.remove('access_token');
            Cookies.remove('user_info');
            delete request.defaults.headers.common['Authorization'];
            commit('LOG_OUT');
            return Promise.resolve(true);
        }
        catch (error) {
            return Promise.reject(error);
        }
    },
};
export default actions;
